<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="me-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar id="header" slot="header" title="智能维修" right-text="退出登录" @click-right="handleLogout" left-arrow @click-left="onClickLeft">
    </van-nav-bar>
    <div class="mecontent">
      <div class="mecard">
        <div class="cardname">{{ information.name }}</div>
        <div class="cardnum">{{ information.tel }}</div>
        <div class="carddepartment">
          <span class="department_one">{{ information.department }}>{{ information.team }}</span>
          <span class="department_two">{{ information.cargo }}</span>
        </div>
      </div>
      <van-cell-group class="meoptions" style="background:none;">
        <van-field
          label="修改密码"
          right-icon="arrow"
          left-icon="lock"
          disabled
          style="background:none;"
          @click="updataPassword"
        />
        <van-field
          label="关于"
          right-icon="arrow"
          left-icon="column"
          disabled
          style="background:none;"
          @click="aboutBtn"
        />
        <van-field
          label="我提报的故障"
          right-icon="arrow"
          left-icon="bell"
          disabled
          style="background:none;"
          @click="myFault"
        />
        <van-field
          label="我提报的问题"
          right-icon="arrow"
          left-icon="question"
          disabled
          style="background:none;"
          @click="myQuestion"
        />
        <!-- <van-cell @click="handleDocPreview" is-link title="测试文件预览" style="background:none;" right-icon="arrow"></van-cell> -->
        <!-- <van-cell @click="handleGetFile" is-link title="获取文件" style="background:none;" right-icon="arrow"></van-cell> -->
        <!-- <van-cell @click="handleTest" is-link title="测试" style="background:none;" right-icon="arrow"></van-cell> -->
      </van-cell-group>
    </div>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0,
    information: {
      name: '',
      tel: '',
      department: '',
      team: '',
      cargo: ''
    },
    docInteraction: null,
    fileBrowser: null,
    fileReader: null
  }),
  methods: {
    onClickLeft() {
      navigate.back()
    },
    handleLogout () {
      navigate.redirectLogin()
    },
    onReady(header, main) {
      this.headerHeightVW = ((header.h + 1) * 100) / header.w;
      const user = window.localStorage.getItem("user");
      const id = JSON.parse(user).id;
      io.getMeDetails({id})
        .then(data => {
          this.information = data
        })
        .catch(msg => {
          this.$toast.clear()
          this.$toast(msg)
        })
    },

    onShow () {
      const user = window.localStorage.getItem("user");
      const id = JSON.parse(user).id;
      io.getMeDetails({id})
        .then(data => {
          this.information = data;
        })
    },

    updataPassword() {
      navigate.open('reset_password')
    },
    aboutBtn() {
      navigate.open('about')
    },
    myFault() {
      navigate.open('my_fault')
    },
    myQuestion() {
      navigate.open('my_question')
    },

    handleDocPreview () {
      // http://cdn.form.xiaoyaoji.cn/1577686208046_77612

      // this.docInteraction = api.require('docInteraction')

      // api.download({
      //     url: 'http://172.16.109.8:8088/web/content?model=repair_manage.reference_manage_order&id=1&field=file&filename_field=file_name',
      //     savePath: 'fs://xxx/1577686208046_77612',
      //     report: true,
      //     cache: true,
      //     allowResume: true
      // }, (ret, err) => {
      //     if (ret.state == 1) {
      //         //下载成功
      //         this.docInteraction.open({
      //           path: 'fs://xxx/1577686208046_77612'
      //         })
      //     }
      // })

      navigate.open('browser', {title: '111', url: 'http://172.16.109.8:8088/web/content?model=repair_manage.reference_manage_order&id=1&field=file&filename_field=file_name'})

    },

    handleGetFile () {
      if (api.systemType == 'android') {
        if (!this.fileBrowser) {
          this.fileBrowser = api.require('selectFile')
        }

        this.fileBrowser.open(function(ret) {
          if(ret.status){
            alert(JSON.stringify(ret.path));
          }else{
            alert('选择文件不存在');
          }
        })
      }

      if (api.systemType == 'ios') {
        if (!this.fileReader) {
          this.fileReader = api.require('fileReader')
        }

        this.fileReader.open((ret) => {
          if (ret.status) {
            

            this.fileReader.addEventListener({
              target: 'select'
            }, (ret) =>{
              alert(JSON.stringify(ret.urls))
            })
          }
        })
      }
    }
  }
}
</script>

